<template>
  <div class="profile-menu">
    <div
      class="performer-menu__item"
      v-for="item of performerMenu"
      :key="item.id"
      :class="{ profile_menu__active: item.active }"
      @click="setActive(item.id)"
    >
      <p>{{ item.title }}</p>
    </div>
    <div
      class="active-highlight"
      :style="{
        transform: `translateY(${activeHighlightTop}px)`,
        height: `${activeHighlightHeight}px`,
      }"
    ></div>
    <div class="profile-menu__exit">
      <vue-black-button :title="'Выйти'" @click="exit" />
    </div>
  </div>
</template>

<script setup lang="ts">
import {
  ref,
  defineProps,
  onMounted,
  nextTick,
  defineEmits,
  onUnmounted,
} from "vue";
import VueBlackButton from "../Buttons/VueBlackButton.vue";
import { useUserStore } from "@/store/store";
import { useRouter } from "vue-router";

const store = useUserStore();
const router = useRouter();
const emit = defineEmits(["select"]);

const props = defineProps({
  user: {
    type: Number,
  },
});

const performerMenu = ref([
  {
    id: 1,
    title: "Личная информация",
    active: true,
    route: "info",
  },
  {
    id: 2,
    title: "Активные заявки",
    active: false,
    route: "active-demand",
  },
  {
    id: 3,
    title: "Завершенные заявки",
    active: false,
    route: "done-demand",
  },
]);

const exit = () => {
  router.push("/");
  setTimeout(() => {
    store.logOutOfProfile();
  }, 0);
};

const activeHighlightTop = ref(0);
const activeHighlightHeight = ref(0);

const setActive = (id: number) => {
  performerMenu.value.forEach((item) => {
    item.active = item.id === id;
  });
  updateHighlightPosition();
  emit("select", id);
};

const updateHighlightPosition = () => {
  nextTick(() => {
    const activeItem = document.querySelector(
      ".performer-menu__item.profile_menu__active"
    ) as HTMLElement;
    if (activeItem) {
      activeHighlightTop.value = activeItem.offsetTop;
      activeHighlightHeight.value = activeItem.offsetHeight;
    }
  });
};

onMounted(() => {
  updateHighlightPosition();
  window.addEventListener("resize", updateHighlightPosition);
});

onUnmounted(() => {
  window.removeEventListener("resize", updateHighlightPosition);
});
</script>

<style lang="scss">
@import url(../../styles/variable.css);

.profile-menu {
  font-family: "sf-pro-400";
  width: 30%;
  display: flex;
  flex-direction: column;
  align-items: end;
  gap: 30px;
  position: relative;
  border-right: 2px solid var(--yellow);
  height: calc(100vh - 300px);

  @media (max-width: 860px) {
    height: 100%;
    justify-content: start;
    align-items: start;
    width: 100%;
    gap: 20px;
  }

  .performer-menu__item {
    font-size: 20px;
    letter-spacing: -1px;
    text-align: end;
    padding: 7px 10px;
    position: relative;
    z-index: 1;
    cursor: pointer;
    @media (max-width: 860px) {
      width: 100%;
    }

    &.profile_menu__active {
      color: var(--light-black);
    }
  }

  &__exit {
    width: 100%;
    max-width: 250px;
    margin-right: 5px;
    @media (max-width: 860px) {
      max-width: 95%;
    }
  }

  .active-highlight {
    position: absolute;
    right: 0; /* Прижимаем к правому краю */
    width: 100%;
    background-color: var(--yellow);
    border-radius: 15px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    transition: transform 0.3s ease, height 0.3s ease;
    z-index: 0;
    max-width: 255px;
    @media (max-width: 860px) {
      max-width: 100%;
    }
  }
}
</style>
