<template>
  <div class="performer-profile">
    <vue-user-profile v-if="props.tabId === 1" :user-info="getUserInfo!" />
    <div class="order-active__cards" v-if="props.tabId === 2">
      <vue-active-card
        v-for="(card, index) of getUserInfo?.activeOrder"
        :key="index"
        :card="card"
        v-if="getLengthActiveCards > 0"
      />
      <div class="order-active__warning" v-else-if="getLengthActiveCards === 0">
        <p class="order-active__warning--p">Активных заказов нет</p>
      </div>
    </div>
    <div class="order-done__cards" v-if="props.tabId === 3">
      <vue-done-card
        v-for="(card, index) of getUserInfo?.doneOrder"
        :key="index"
        :card="card"
        v-if="getLengthDoneCards > 0"
      />
      <div class="order-done__warning" v-else-if="getLengthDoneCards === 0">
        <p class="order-done__warning--p">Завершенных заказов нет</p>
      </div>
    </div>
  </div>
</template>
<script setup lang="ts">
import { computed, defineProps } from "vue";
import performerInfo from "../../localDatabase/performerInfo.json";
import VueUserProfile from "../Forms/VueUserProfile.vue";
import PerformerInfo from "@/interfaces/PerformerInfo";
import VueDoneCard from "../Cards/VueDoneCard.vue";
import VueActiveCard from "../Cards/VueActiveCard.vue";

const props = defineProps({
  user: Number,
  tabId: Number,
});

const getUserInfo = computed(() => {
  if (props.user === 1) {
    return performerInfo as PerformerInfo;
  } else if (props.user === 0) {
    return performerInfo as PerformerInfo; // TODO: заменить на другой json
  }
});

const getLengthDoneCards = computed((): number => {
  if (getUserInfo) {
    return getUserInfo.value!.doneOrder.length;
  } else return 0;
});

const getLengthActiveCards = computed((): number => {
  if (getUserInfo) {
    return getUserInfo.value!.activeOrder.length;
  } else return 0;
});
</script>
<style lang="scss">
@mixin warning {
  background-color: var(--error);
  padding: 15px;
  border-radius: 15px;
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
  overflow: hidden;
  animation: showWarning 1s ease-out;
  &--p {
    color: var(--white);
    text-align: end;
    text-wrap: nowrap;
  }
  @keyframes showWarning {
    0% {
      width: 0px;
      opacity: 0;
    }
    90% {
      width: calc(100% + 10px);
    }
    100% {
      opacity: 1;
      width: calc(100%);
    }
  }
}

.performer-profile {
  font-family: "sf-pro-400";
  font-size: 20px;
}

.order {
  &-done {
    &__warning {
      @include warning();
    }
  }
  &-active {
    &__warning {
      @include warning();
    }
  }
}
</style>
