<template>
  <div class="user-profile" v-if="!isEditInfo">
    <div class="user__lastName">
      <label>Фамилия</label>
      <p>{{ store.user.lastName }}</p>
    </div>
    <div class="user__name">
      <label>Имя</label>
      <p>{{ store.user.name }}</p>
    </div>
    <div class="user__serName">
      <label>Отчество</label>
      <p>{{ store.user.serName }}</p>
    </div>
    <div class="user__phone">
      <label>Номер телефона</label>
      <vue-number-input v-model="userPhone" />
    </div>
    <div class="user__date">
      <label>Дата регистрации</label>
      <p>{{ store.user.startDate }}</p>
    </div>
    <div class="user__edit-info">
      <vue-yellow-button
        :title="'Изменить'"
        @click="isEditInfo = !isEditInfo"
      />
    </div>
    <div class="user__sum">
      <p>Заработали за все время</p>
      <label class="user__sum--label">{{ user.sum }}руб</label>
    </div>
  </div>

  <div class="user-profile" v-else>
    <div class="user__lastName">
      <label>Фамилия</label>
      <vue-text-input
        v-model="userEditInfo.userLastName"
        @update:value="userEditInfo.userLastName.value = $event"
      />
    </div>
    <div class="user__name">
      <label>Имя</label>
      <vue-text-input
        v-model="userEditInfo.userName"
        @update:value="userEditInfo.userName.value = $event"
      />
    </div>
    <div class="user__serName">
      <label>Отчество</label>
      <vue-text-input
        v-model="userEditInfo.userSerName"
        @update:value="userEditInfo.userSerName.value = $event"
      />
    </div>
    <div class="user__phone">
      <label>Номер телефона</label>
      <vue-number-input
        v-model="userEditInfo.userPhone"
        @update:value="userEditInfo.userPhone.value.value = $event"
      />
    </div>
    <div class="user__date">
      <label>Дата регистрации</label>
    </div>
    <div class="user__edit-info">
      <vue-yellow-button :title="'Сохранить'" @click="updateInfoUser" />
    </div>
  </div>
</template>

<script lang="ts" setup>
import { defineProps, ref } from "vue";
import PerformerInfo from "@/interfaces/PerformerInfo";
import VueYellowButton from "../Buttons/VueYellowButton.vue";
import VueNumberInput from "../Inputs/VueNumberInput.vue";
import { InputNumber } from "@/interfaces/InputNumberInterface";
import VueTextInput from "../Inputs/VueTextInput.vue";
import { useUserProfileStore } from "@/store/profile";
import { EditUser } from "@/store/profile";

const store = useUserProfileStore();

const props = defineProps({
  userInfo: {
    type: Object as () => PerformerInfo,
    required: true,
  },
});

const userEditInfo = {
  userName: {
    value: store.user.name,
    isValid: ref(null),
    placeholder: "Имя",
  },
  userLastName: {
    value: store.user.lastName,
    isValid: ref(null),
    placeholder: "Фамилия",
  },
  userSerName: {
    value: store.user.serName,
    isValid: ref(null),
    placeholder: "Отчество",
  },
  userPhone: {
    value: ref(store.user.phone),
    isValid: ref(null),
    type: "phone",
    disabled: false,
  },
};

const isEditInfo = ref(false);

const updateInfoUser = () => {
  const user: EditUser = {
    userName: userEditInfo.userName.value,
    userLastName: userEditInfo.userLastName.value,
    userSerName: userEditInfo.userSerName.value,
    userPhone: userEditInfo.userPhone.value.value,
  };
  store.updateUserInfo(user);
  userPhone.value.value = store.user.phone;
  isEditInfo.value = !isEditInfo.value;
};

const user = props.userInfo;

const userPhone: InputNumber = {
  value: ref(store.user.phone),
  type: "phone",
  isValid: ref(null),
  disabled: true,
};
</script>

